import React, { useState, useEffect } from "react";
import {
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow,
  CTable,
  CTableBody,
  CTableDataCell,
  CTableHead,
  CTableHeaderCell,
  CTableRow,
  CButton,
  CButtonGroup,
} from "@coreui/react";
import { useHistory } from "react-router-dom";

import Loader from "../../components/Loader";
import Message from "../../components/Message";
import { Authentification } from "../../components/index";

import { getOrders } from "../../provider/orderFetch";

const Commandes = () => {
  /** State */
  const [orders, setOrders] = useState(null);
  const [error, setError] = useState(null);
  const [status, setStatus] = useState(null);

  /** Hook Route */
  let history = useHistory();

  /** Run once, when the component mounts */
  useEffect(() => {
    console.log("Run once, when the component mounts");

    async function fetch() {
      const resp = await getOrders();

      resp?.status ? setOrders([...resp?.data]) : setError(resp?.error);
    }

    fetch();
  }, []);

  const changeStatus = async (status) => {
    const resp = await getOrders(null, status);

    setStatus(status);

    if (resp.status) {
      setOrders([...resp?.data]);
    }
  };

  const handleNavigation = (id) => {
    history.push(`/detail-commande/${id}`);
  };

  if (orders === null) {
    return <Loader />;
  }

  if (error) {
    return <Message> {error}</Message>;
  }

  return (
    <CRow>
      <Authentification />
      <CCol xs={12}>
        <CCard className="mb-4">
          <CCardHeader>
            <CRow>
              <CCol sm={5}>
                <strong>Listes des commandes</strong>
              </CCol>
              <CCol sm={7} className="d-none d-md-block">
                <CButtonGroup className="float-end me-3">
                  {["CONFIRMED", "READING", "DECLINED_BY_MERCHANT"].map(
                    (value) => (
                      <CButton
                        color="outline-secondary"
                        key={value}
                        className="mx-0"
                        active={value === status}
                        onClick={() => changeStatus(value)}
                      >
                        {value}
                      </CButton>
                    )
                  )}
                </CButtonGroup>
              </CCol>
            </CRow>
          </CCardHeader>
          <CCardBody>
            <CTable>
              <CTableHead>
                <CTableRow>
                  <CTableHeaderCell scope="col">Numero</CTableHeaderCell>
                  <CTableHeaderCell scope="col">Email</CTableHeaderCell>
                  <CTableHeaderCell scope="col">Status</CTableHeaderCell>
                  <CTableHeaderCell scope="col">
                    Date de creation
                  </CTableHeaderCell>
                  <CTableHeaderCell scope="col">Actions</CTableHeaderCell>
                </CTableRow>
              </CTableHead>
              <CTableBody>
                {orders.length > 0 ? (
                  orders.map((order) => (
                    <CTableRow key={order._id}>
                      <CTableDataCell>{order.numero}</CTableDataCell>
                      <CTableDataCell>{order.email}</CTableDataCell>
                      <CTableDataCell>{order.status}</CTableDataCell>
                      <CTableDataCell>{order.createdAt}</CTableDataCell>
                      <CTableDataCell>
                        <CButton
                          color={"primary"}
                          className="me-1"
                          onClick={() => handleNavigation(order._id)}
                        >
                          {" "}
                          Voir{" "}
                        </CButton>
                      </CTableDataCell>
                    </CTableRow>
                  ))
                ) : (
                  <h3>{"NO Items"}</h3>
                )}
              </CTableBody>
            </CTable>
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
  );
};

export default Commandes;
